import { createState, useState } from '@hookstate/core';

const globalState = createState({
    state: false,
    title: '',
    desc: '',
    type: 'error',
    onClose: () => null
});
const wrapState = (s) => ({
    get: () => s.value,
    set: (value) => s.set(value),
});
export const useAlertState = () => wrapState(useState(globalState));
